export default function DashboardPage() {
  return (
    <div className="page-inner">

      {/* Page header */}
      <div className="page-header">
        <div>
          <h1 className="page-title">Dashboard</h1>
          <p className="page-subtitle">
            Overview of revenue and traffic
          </p>
        </div>
      </div>

      {/* Stats cards */}
      <section className="dashboard-grid">
        <article className="dashboard-card">
          <span className="card-label">Revenue Today</span>
          <strong className="card-value">$1,240</strong>
        </article>

        <article className="dashboard-card">
          <span className="card-label">Impressions</span>
          <strong className="card-value">1.2M</strong>
        </article>

        <article className="dashboard-card">
          <span className="card-label">Active Campaigns</span>
          <strong className="card-value">42</strong>
        </article>

        <article className="dashboard-card">
          <span className="card-label">Fill Rate</span>
          <strong className="card-value">96%</strong>
        </article>
      </section>

      {/* Chart */}
      <section className="dashboard-chart">
        <div className="chart-placeholder">
          Revenue / Requests (24h)
        </div>
      </section>

    </div>
  );
}


